<template><section>
<h2>移动端适配</h2>
<p>当前的适配比例是 1/37.5</p>
<pre v-pre=""><code class="hljs language-javascript">  <span class="hljs-keyword">import</span> { ReponeFont } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  <span class="hljs-comment">// 一般都是调用一次初始化 直接 ReponeFont.init()</span>
  Vue.use(ReponeFont)
  <span class="hljs-keyword">this</span>.$reponeFont.init()
  <span class="hljs-comment">// 访问 this.$reponeFont</span>
</code></pre>
<h4>演示</h4>
<p>触发init会自动引入 移动端适配的css和 js</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickReponeFont\&quot;&gt;点击适配&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickReponeFont () {\n      this.$reponeFont.init()\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickReponeFont">点击适配</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickReponeFont"</span>&gt;</span>点击适配<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickReponeFont () {
      <span class="hljs-keyword">this</span>.$reponeFont.init()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  methods: {
    onClickReponeFont () {
      this.$reponeFont.init()
    }
  }
}
</script>


